/**
 * Code Examples Configuration
 * 
 * Before/after code examples for TypeScript and Python showing how easy it is to integrate AgentGuard.
 */

export const codeExamples = {
  typescript: {
    before: `import OpenAI from 'openai';

const client = new OpenAI({
  apiKey: process.env.OPENAI_API_KEY,
});

const response = await client.chat.completions.create({
  model: 'gpt-4',
  messages: [{ role: 'user', content: userInput }],
});

// ❌ No security checks
// ❌ No cost tracking
// ❌ No budget limits`,
    after: `import { GuardedOpenAI } from 'agentguard-sdk';

const client = new GuardedOpenAI({
  apiKey: process.env.OPENAI_API_KEY,
  guardrails: ['pii-detection', 'prompt-injection'],
  budget: { daily: 100 },
});

const response = await client.chat.completions.create({
  model: 'gpt-4',
  messages: [{ role: 'user', content: userInput }],
});

// ✅ PII automatically detected and blocked
// ✅ Costs tracked in real-time
// ✅ Budget limits enforced`,
  },
  python: {
    before: `from openai import OpenAI

client = OpenAI(
    api_key=os.getenv("OPENAI_API_KEY")
)

response = client.chat.completions.create(
    model="gpt-4",
    messages=[{"role": "user", "content": user_input}]
)

# ❌ No security checks
# ❌ No cost tracking
# ❌ No budget limits`,
    after: `from agentguard import GuardedOpenAI

client = GuardedOpenAI(
    api_key=os.getenv("OPENAI_API_KEY"),
    guardrails=["pii-detection", "prompt-injection"],
    budget={"daily": 100}
)

response = client.chat.completions.create(
    model="gpt-4",
    messages=[{"role": "user", "content": user_input}]
)

# ✅ PII automatically detected and blocked
# ✅ Costs tracked in real-time
# ✅ Budget limits enforced`,
  },
} as const;

export type CodeExamples = typeof codeExamples;
