import React from 'react';
import { Card } from '@/components/ui';
import { Check, Zap, Shield } from 'lucide-react';
import { siteConfig } from '@/lib/config';

export function Solution() {
  const { solution } = siteConfig;

  return (
    <section className="py-20">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="text-center max-w-3xl mx-auto mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            {solution.title}
          </h2>
          <p className="text-lg text-foreground/70">
            {solution.description}
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-4xl mx-auto mb-16">
          {solution.features.map((feature, index) => (
            <Card key={index} variant="bordered" className="space-y-3">
              <div className="flex items-start gap-3">
                <div className="flex-shrink-0 w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                  {index === 0 ? <Zap className="w-5 h-5 text-primary" /> : <Shield className="w-5 h-5 text-primary" />}
                </div>
                <div>
                  <h3 className="text-xl font-bold mb-2">{feature.title}</h3>
                  <p className="text-foreground/70">{feature.description}</p>
                </div>
              </div>
            </Card>
          ))}
        </div>

        {/* Supported Clients & Guardrails */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-4xl mx-auto">
          {/* Supported Clients */}
          <Card variant="elevated" className="space-y-4">
            <h3 className="text-lg font-bold">Supported AI Providers</h3>
            <div className="space-y-2">
              {solution.clients.map((client, index) => (
                <div key={index} className="flex items-center gap-2">
                  <Check className="w-5 h-5 text-success flex-shrink-0" />
                  <span className="text-foreground/80">{client}</span>
                </div>
              ))}
            </div>
          </Card>

          {/* Built-in Guardrails */}
          <Card variant="elevated" className="space-y-4">
            <h3 className="text-lg font-bold">Built-in Guardrails</h3>
            <div className="space-y-2">
              {solution.guardrails.map((guardrail, index) => (
                <div key={index} className="flex items-center gap-2">
                  <Check className="w-5 h-5 text-success flex-shrink-0" />
                  <span className="text-foreground/80">{guardrail}</span>
                </div>
              ))}
            </div>
          </Card>
        </div>

        {/* Open Source Badge */}
        {solution.openSource && (
          <div className="text-center mt-12">
            <div className="inline-flex items-center gap-2 px-6 py-3 bg-success/10 text-success rounded-full font-medium">
              <Check className="w-5 h-5" />
              <span>100% Open Source • MIT License</span>
            </div>
          </div>
        )}
      </div>
    </section>
  );
}
