import React from 'react';
import { Button } from '@/components/ui';
import { Github, ArrowRight } from 'lucide-react';
import { siteConfig } from '@/lib/config';

export function Hero() {
  const { hero } = siteConfig;

  return (
    <section className="relative py-20 md:py-32 overflow-hidden">
      {/* Background gradient */}
      <div className="absolute inset-0 bg-gradient-to-br from-primary/5 via-background to-background -z-10" />
      
      <div className="container mx-auto px-4">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          {/* Text Content */}
          <div className="space-y-8">
            <div className="space-y-4">
              <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold leading-tight">
                <span className="bg-gradient-to-r from-primary to-primary/60 bg-clip-text text-transparent">
                  {hero.headline}
                </span>
              </h1>
              <p className="text-lg md:text-xl text-foreground/70 leading-relaxed">
                {hero.subheadline}
              </p>
            </div>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row gap-4">
              <Button
                variant="primary"
                size="lg"
                href={hero.cta.primary.href}
                icon={<ArrowRight size={20} />}
              >
                {hero.cta.primary.text}
              </Button>
              <Button
                variant="outline"
                size="lg"
                href={hero.cta.secondary.href}
                icon={<Github size={20} />}
              >
                {hero.cta.secondary.text}
              </Button>
            </div>

            {/* Trust Indicators */}
            <div className="flex flex-wrap items-center gap-6 pt-4">
              <div className="flex items-center gap-2 text-sm text-foreground/60">
                <svg className="w-5 h-5 text-success" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                <span>100% Open Source</span>
              </div>
              <div className="flex items-center gap-2 text-sm text-foreground/60">
                <svg className="w-5 h-5 text-success" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                <span>MIT License</span>
              </div>
              <div className="flex items-center gap-2 text-sm text-foreground/60">
                <svg className="w-5 h-5 text-success" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                <span>5 Minute Setup</span>
              </div>
            </div>
          </div>

          {/* Visual/Illustration */}
          <div className="relative">
            <div className="relative bg-gradient-to-br from-primary/10 to-primary/5 rounded-2xl p-8 border border-primary/20">
              {/* Code snippet preview */}
              <div className="bg-gray-900 dark:bg-gray-950 rounded-lg p-6 font-mono text-sm">
                <div className="text-gray-400 mb-2">// Drop-in replacement</div>
                <div className="text-gray-100">
                  <span className="text-purple-400">import</span>
                  {' { '}
                  <span className="text-blue-400">GuardedOpenAI</span>
                  {' } '}
                  <span className="text-purple-400">from</span>
                  {' '}
                  <span className="text-green-400">'agentguard-sdk'</span>
                  ;
                </div>
                <div className="mt-4 text-gray-100">
                  <span className="text-purple-400">const</span>
                  {' client = '}
                  <span className="text-purple-400">new</span>
                  {' '}
                  <span className="text-blue-400">GuardedOpenAI</span>
                  {'({'}
                </div>
                <div className="ml-4 text-gray-100">
                  <span className="text-blue-300">guardrails</span>
                  {': ['}
                  <span className="text-green-400">'pii-detection'</span>
                  {'],'}
                </div>
                <div className="ml-4 text-gray-100">
                  <span className="text-blue-300">budget</span>
                  {': { '}
                  <span className="text-blue-300">daily</span>
                  {': '}
                  <span className="text-orange-400">100</span>
                  {' }'}
                </div>
                <div className="text-gray-100">{'});'}</div>
              </div>

              {/* Floating badges */}
              <div className="absolute -top-4 -right-4 bg-success text-white px-4 py-2 rounded-full text-sm font-medium shadow-lg">
                ✓ Secure
              </div>
              <div className="absolute -bottom-4 -left-4 bg-primary text-white px-4 py-2 rounded-full text-sm font-medium shadow-lg">
                ✓ Cost Controlled
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
