'use client';

import React, { useState } from 'react';
import { Tabs, CodeBlock } from '@/components/ui';
import { codeExamples } from '@/lib/code-examples';

export function CodeExample() {
  return (
    <section className="py-20 bg-secondary/30">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="text-center max-w-3xl mx-auto mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            See the Difference
          </h2>
          <p className="text-lg text-foreground/70">
            Add security and cost control with just a few lines of code. No architecture changes required.
          </p>
        </div>

        {/* Code Comparison */}
        <div className="max-w-6xl mx-auto">
          <Tabs
            tabs={[
              {
                id: 'typescript',
                label: 'TypeScript',
                content: (
                  <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    {/* Before */}
                    <div className="space-y-3">
                      <div className="flex items-center gap-2">
                        <div className="w-3 h-3 rounded-full bg-destructive" />
                        <h3 className="font-bold text-lg">Before</h3>
                      </div>
                      <CodeBlock
                        code={codeExamples.typescript.before}
                        language="typescript"
                      />
                    </div>

                    {/* After */}
                    <div className="space-y-3">
                      <div className="flex items-center gap-2">
                        <div className="w-3 h-3 rounded-full bg-success" />
                        <h3 className="font-bold text-lg">After</h3>
                      </div>
                      <CodeBlock
                        code={codeExamples.typescript.after}
                        language="typescript"
                      />
                    </div>
                  </div>
                ),
              },
              {
                id: 'python',
                label: 'Python',
                content: (
                  <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    {/* Before */}
                    <div className="space-y-3">
                      <div className="flex items-center gap-2">
                        <div className="w-3 h-3 rounded-full bg-destructive" />
                        <h3 className="font-bold text-lg">Before</h3>
                      </div>
                      <CodeBlock
                        code={codeExamples.python.before}
                        language="python"
                      />
                    </div>

                    {/* After */}
                    <div className="space-y-3">
                      <div className="flex items-center gap-2">
                        <div className="w-3 h-3 rounded-full bg-success" />
                        <h3 className="font-bold text-lg">After</h3>
                      </div>
                      <CodeBlock
                        code={codeExamples.python.after}
                        language="python"
                      />
                    </div>
                  </div>
                ),
              },
            ]}
          />
        </div>

        {/* Key Changes Highlight */}
        <div className="max-w-4xl mx-auto mt-12 text-center">
          <p className="text-foreground/70">
            Just change <code className="px-2 py-1 bg-primary/10 text-primary rounded">OpenAI</code> to{' '}
            <code className="px-2 py-1 bg-success/10 text-success rounded">GuardedOpenAI</code> and add your guardrails. That's it.
          </p>
        </div>
      </div>
    </section>
  );
}
