import React from 'react';
import { Card } from '@/components/ui';
import { ShieldAlert, TrendingUp, Unplug, AlertTriangle } from 'lucide-react';
import { siteConfig } from '@/lib/config';

const iconMap = {
  'shield-alert': ShieldAlert,
  'trending-up': TrendingUp,
  'unplug': Unplug,
};

export function Problem() {
  const { problem } = siteConfig;

  return (
    <section className="py-20 bg-secondary/30">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="text-center max-w-3xl mx-auto mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            {problem.title}
          </h2>
          <p className="text-lg text-foreground/70">
            {problem.description}
          </p>
        </div>

        {/* Problems Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-12">
          {problem.problems.map((item, index) => {
            const Icon = iconMap[item.icon as keyof typeof iconMap];
            return (
              <Card key={index} variant="bordered" className="text-center space-y-4">
                <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-destructive/10 text-destructive">
                  <Icon size={32} />
                </div>
                <h3 className="text-xl font-bold">{item.title}</h3>
                <p className="text-foreground/70">{item.description}</p>
              </Card>
            );
          })}
        </div>

        {/* Story Callout */}
        {problem.story.enabled && (
          <Card variant="elevated" className="max-w-4xl mx-auto bg-warning/5 border-l-4 border-warning">
            <div className="flex gap-4">
              <div className="flex-shrink-0">
                <AlertTriangle className="w-6 h-6 text-warning" />
              </div>
              <div>
                <h4 className="font-bold text-lg mb-2">Real Story</h4>
                <p className="text-foreground/80 leading-relaxed">
                  {problem.story.text}
                </p>
              </div>
            </div>
          </Card>
        )}
      </div>
    </section>
  );
}
