import React from 'react';
import { Card } from '@/components/ui';
import { Check, X } from 'lucide-react';
import { comparison } from '@/lib/comparison';

export function Comparison() {
  return (
    <section className="py-20 bg-secondary/30">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="text-center max-w-3xl mx-auto mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            {comparison.title}
          </h2>
          <p className="text-lg text-foreground/70">
            {comparison.description}
          </p>
        </div>

        {/* Desktop Table */}
        <div className="hidden md:block max-w-5xl mx-auto">
          <Card variant="bordered" className="overflow-hidden">
            <table className="w-full">
              <thead>
                <tr className="border-b border-border">
                  <th className="text-left p-4 font-bold">Feature</th>
                  <th className="text-center p-4 font-bold bg-primary/5">
                    <div className="flex items-center justify-center gap-2">
                      <span>AgentGuard</span>
                      <span className="px-2 py-1 bg-primary text-primary-foreground text-xs rounded-full">Recommended</span>
                    </div>
                  </th>
                  <th className="text-center p-4 font-bold">Manual Implementation</th>
                </tr>
              </thead>
              <tbody>
                {comparison.rows.map((row, index) => (
                  <tr key={index} className="border-b border-border last:border-0">
                    <td className="p-4 font-medium">{row.feature}</td>
                    <td className="p-4 text-center bg-primary/5">
                      {typeof row.agentguard === 'boolean' ? (
                        row.agentguard ? (
                          <Check className="w-6 h-6 text-success mx-auto" />
                        ) : (
                          <X className="w-6 h-6 text-destructive mx-auto" />
                        )
                      ) : (
                        <span className="text-foreground/80">{row.agentguard}</span>
                      )}
                    </td>
                    <td className="p-4 text-center">
                      {typeof row.manual === 'boolean' ? (
                        row.manual ? (
                          <Check className="w-6 h-6 text-success mx-auto" />
                        ) : (
                          <X className="w-6 h-6 text-destructive mx-auto" />
                        )
                      ) : (
                        <span className="text-foreground/60">{row.manual}</span>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </Card>
        </div>

        {/* Mobile Cards */}
        <div className="md:hidden space-y-4 max-w-lg mx-auto">
          {comparison.rows.map((row, index) => (
            <Card key={index} variant="bordered" className="space-y-3">
              <h3 className="font-bold">{row.feature}</h3>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1">
                  <div className="text-sm text-foreground/60">AgentGuard</div>
                  <div className="font-medium">
                    {typeof row.agentguard === 'boolean' ? (
                      row.agentguard ? (
                        <Check className="w-5 h-5 text-success" />
                      ) : (
                        <X className="w-5 h-5 text-destructive" />
                      )
                    ) : (
                      row.agentguard
                    )}
                  </div>
                </div>
                <div className="space-y-1">
                  <div className="text-sm text-foreground/60">Manual</div>
                  <div className="font-medium text-foreground/70">
                    {typeof row.manual === 'boolean' ? (
                      row.manual ? (
                        <Check className="w-5 h-5 text-success" />
                      ) : (
                        <X className="w-5 h-5 text-destructive" />
                      )
                    ) : (
                      row.manual
                    )}
                  </div>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
