import React from 'react';
import { Card } from '@/components/ui';
import { ShieldCheck, ShieldAlert, Shield, DollarSign, TrendingUp, Zap } from 'lucide-react';
import { features } from '@/lib/features';

const iconMap = {
  'shield-check': ShieldCheck,
  'shield-alert': ShieldAlert,
  'shield': Shield,
  'dollar-sign': DollarSign,
  'trending-up': TrendingUp,
  'zap': Zap,
};

const categoryColors = {
  security: 'text-blue-500',
  cost: 'text-green-500',
  dx: 'text-purple-500',
};

export function Features() {
  return (
    <section className="py-20">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="text-center max-w-3xl mx-auto mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            Everything You Need
          </h2>
          <p className="text-lg text-foreground/70">
            Comprehensive security and cost control features built into one SDK.
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 max-w-6xl mx-auto">
          {features.map((feature) => {
            const Icon = iconMap[feature.icon as keyof typeof iconMap];
            const colorClass = categoryColors[feature.category];

            return (
              <Card key={feature.id} variant="bordered" className="space-y-4 hover:shadow-lg transition-shadow">
                <div className={`inline-flex items-center justify-center w-12 h-12 rounded-lg bg-${feature.category === 'security' ? 'blue' : feature.category === 'cost' ? 'green' : 'purple'}-500/10`}>
                  <Icon className={`w-6 h-6 ${colorClass}`} />
                </div>
                <h3 className="text-xl font-bold">{feature.title}</h3>
                <p className="text-foreground/70 leading-relaxed">
                  {feature.description}
                </p>
              </Card>
            );
          })}
        </div>
      </div>
    </section>
  );
}
